import axios from 'axios';
import { useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import _ from 'lodash';
import { useImmer } from 'use-immer';
import { Avatar, Button } from 'antd';
import { UserOutlined } from '@ant-design/icons';

export default function ViewVote() {
  const params = useParams();
  const voteId = params.id;
  const [voteInfo, setVoteInfo] = useState<any>(null);
  const [userInfo, setUserInfo] = useState<any>({});
  const navigate = useNavigate();
  useEffect(() => {
    axios.get('/account/current-user').then((res) => {
      setUserInfo(res.data.result);
    });
  }, []);

  useEffect(() => {
    // res = {
    //   code: 0,
    //   result: {
    //     vote 
    //     options 
    //     userVotes 
    //   }
    // }
    axios.get('/vote/' + voteId).then((res) => {
      setVoteInfo(res.data.result);
    });
  }, [voteId]);
  // 
  useEffect(() => {
    // 3000,8081,
    const ws = new WebSocket(`ws://${window.location.host}/realtime-voteinfo/${voteId}`);
    ws.onmessage = function (e) {
      const votes = JSON.parse(e.data);
      setVoteInfo((voteInfo: any) => {
        return {
          ...voteInfo,
          userVotes: votes,
        };
      });
    };

    return () => {
      ws.close();
      ws.onmessage = null;
    };
  }, [voteId]);
  console.log(voteInfo);
  const [selectedOptionIds, updateSelectedOptionIds] = useImmer<any[]>([]);
  function handleOptionClick(option: any) {
    // 
    if (voteInfo.vote.anonymous) {
      // ids draft
      updateSelectedOptionIds((ids) => {
        if (ids.includes(option.optionId)) {
          // ,
          return ids.filter((it) => it !== option.optionId);
          // 
        } else {
          ids.push(option.optionId);
        }
      });
      // 
    } else {
      voteOption(option);
    }
  }
  if (!voteInfo) {
    return <span>...</span>;
  }
  async function voteOption(option: any) {
    const voteId = option.voteId;
    const optionId = option.optionId;
    //  
    await axios.post('/vote/' + voteId, {
      optionIds: [optionId],
    });
    console.log('');
  }
  async function submit() {
    const voteId = voteInfo.vote.voteId;
    await axios.post('/vote/' + voteId, {
      optionIds: selectedOptionIds,
    });
  }
  // console.log(selectedOptionIds);
  // console.log(voteInfo.vote.anonymous);
  // {
  //   42: [{}, {}],
  //   45: [{}, {}]
  // }
  // 
  const groupedVotes = _.groupBy(voteInfo.userVotes, 'optionId');
  // 
  const totalVoteUsersCount = _.uniq(voteInfo.userVotes.map((it: any) => it.userId)).length;
  // 
  const currentUserVoted = !!voteInfo.userVotes.find((vote: any) => vote.userId == userInfo.userId);
  return (
    <div style={{ justifyContent: 'space-between', display: 'flex', flexDirection: 'column', height: '100%' }}>
      <div>
        <h3>: {voteInfo.vote.title}</h3>
        <h3>: {voteInfo.vote.desc}</h3>
      </div>
      <div style={{ maxHeight: '400px', overflow: 'auto' }}>
        {voteInfo.options.map((option: any) => {
          return (
            <div key={option.optionId} style={{ height: '120px' }} onClick={() => handleOptionClick(option)}>
              {/*  */}
              <h3>
                <span>{option.content}</span>

                {/*  */}
                {voteInfo.vote.anonymous && !currentUserVoted ? (
                  <span className="">
                    {selectedOptionIds.find((id) => id == option.optionId) ? '' : ''}
                  </span>
                ) : (
                  //  or 
                  <span className="">
                    {(groupedVotes[option.optionId] ?? []).find((it) => it.userId == userInfo.userId) ? '' : ''}
                  </span>
                )}
                <span style={{ float: 'right' }}>{groupedVotes[option.optionId]?.length ?? 0}</span>
              </h3>
              <progress value={`${(groupedVotes[option.optionId]?.length ?? 0) / totalVoteUsersCount}`} />

              {/*  */}
              <div>
                {!voteInfo.vote.anonymous &&
                  (groupedVotes[option.optionId] ?? []).map((vote, idx) => {
                    // 
                    return (
                      <Avatar
                        key={idx}
                        icon={<UserOutlined />}
                        src={vote.avatar?.replace(`${window.location.origin}`, '')}
                      />
                    );
                  })}
              </div>
            </div>
          );
        })}
      </div>

      <div>: {new Date(voteInfo.vote.deadline).toLocaleString()}</div>
      {/* button */}
      {/*  */}
      {/*  */}
      {!!voteInfo.vote.anonymous && !currentUserVoted && (
        <Button type="primary" size="large" disabled={selectedOptionIds.length <= 0} onClick={submit}>
          
        </Button>
      )}
      <Button type="ghost" size="large">
        <Link to="/home/me" style={{ color: 'white', fontWeight: '900' }}>
          
        </Link>
      </Button>
    </div>
  );
}
